B;MAAtB,8BAAsB;MAAtB,8BAAsB;SAAtB,4BAAsB;SAAtB,2BAAsB;UAAtB,0BAAsB;cAAtB,sBAAsB;CAAE;AAzLnE;MA0LiB,4BAA4B;CAAE;CAAA;AAI/C;EAII,WAAU;CACX;AACD;EACE,mBAL0B;EAM1B,8BAA6B;EAC7B,sCAA6B;EAA7B,iCAA6B;EAA7B,mCAA6B;EAA7B,8BAA6B;CAsB9B;AC9YC;ADqXF;MAMI,sBAAyB;MAAzB,kCAAyB;SAAzB,mBAAyB;UAAzB,mBAAyB;cAAzB,0BAAyB;MACzB,0BAAmB;MAAnB,4BAAmB;SAAnB,uBAAmB;UAAnB,uBAAmB;cAAnB,oBAAmB;MACnB,qBAAoB;MACpB,iCAAwB;MAAxB,4BAAwB;MAAxB,8BAAwB;MAAxB,yBAAwB;CAgB3B;AAfG;QACE,gBAAe;CAChB;CAAA;ACjYH;ADqXF;MAeI,6BAAgC;MAAhC,+BAAgC;MAAhC,yCAAgC;SAAhC,0BAAgC;SAAhC,4BAAgC;UAAhC,qCAAgC;cAAhC,iCAAgC;MAChC,sBAAyB;MAAzB,kCAAyB;SAAzB,mBAAyB;UAAzB,mBAAyB;cAAzB,0BAAyB;MACzB,0BAAmB;MAAnB,4BAAmB;SAAnB,uBAAmB;UAAnB,uBAAmB;cAAnB,oBAAmB;MACnB,uCAAuD;SAAvD,oCAAuD;UAAvD,mCAAuD;WAAvD,kCAAuD;cAAvD,+BAAuD;MACvD,8BAA+C;CAMlD;AAzBA;QAqBK,kBAAiB;QACjB,iCAAuD;CACxD;CAAA;AAGL;EACE,uBAAsB;CAUvB;CATC;IACE,YAAW;CAIZ;CALD;MAGI,oBAAO;MAAP,gBAAO;SAAP,iBAAO;UAAP,YAAO;cAAP,QAAO;CACR;AANL;IASI,yBAAwB;CACzB;AAIL;EACE,cAAa;EACb,kBAAiB;EACjB,oBAAmB;EACnB,eAAc;EACd,iBAAgB;CAiBjB;AAtBD;IAOI,4BAAoB;IAApB,6BAAoB;IAApB,yBAAoB;IAApB,4BAAoB;IAApB,qBAAoB;IACpB,eE5d8B;CFoe/B;AAhBH;MAUM,oBAAmB;CACpB;AAXL;MAaM,aAAY;MACZ,oBAAmB;CACpB;AAfL;IAiBuB,kBAAiB;CAAI;AC9axC;AD6ZJ;MAmBI,eAAc;MACd,6BAAS;MAAT,kBAAS;SAAT,0BAAS;UAAT,mBAAS;cAAT,UAAS;CAEZ;CAAA","file":"index.vue?vue&type=style&index=0&id=4d17fef1&lang=scss&scoped=true&","sourcesContent":["\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"~@/styles/theme/colors\";\n@import \"~@/styles/theme/breaks\";\n\n$topoff: 8rem;\n$sidebar-pad:   2rem 1.5rem;\n$sidebar-width: 18rem;\n$ribbon-height: 2.25rem;\n$footer-height: 60px;\n\n%rack {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  > * + * { margin-left: 1rem }\n}\n\n.WineGrid {\n  flex: 1;\n  &Wrapper {\n    position: relative;\n    padding-left: 1.5rem;\n    padding-right: 1.5rem;\n    overflow: visible;\n    padding-top: 8rem !important;\n    @include Break( max-width Breaks(3) ){\n      padding-top: 6rem !important;\n    }\n  }\n}\n\n.WineFilters {\n\n  $B: #{&};\n  $OPEN: '.open';\n\n  position: sticky;\n  top: -1px;\n  z-index: 5;\n\n  justify-content: flex-start;\n  flex: 0 1 $sidebar-width !important;\n  align-self: flex-start;\n\n  height: 100vh;\n  max-height: 100vh;\n  min-width: $sidebar-width;\n  width: $sidebar-width;\n  max-width: $sidebar-width;\n  overflow: visible;\n  margin: $topoff 0 0;\n  margin: $topoff 0 1px;\n\n  // border-right: 1px solid rgba(13,13,13,.13);\n  box-shadow: -.5em 0 0 -1.5em rgba(black,0);\n  \n  transition: .38s ease-out;\n  transition-property: opacity transform margin-left;\n  \n  &#{$OPEN} {\n    // overflow: hidden;\n    // box-shadow: 0 0 3em 0 rgba(black,.1);\n    // box-shadow: 1.5em -1.5em 3em -1em rgba(black,.1);\n  }\n  &:not(#{$OPEN}) {\n    transform: translateX(-100%);\n    margin-left: -$sidebar-width;\n    #{$B}--ribbon {\n      padding-right: 3rem;\n      /deep/ .UiIcon {\n        opacity: 1 !important;\n        right: 1.5rem!important;\n        margin: 0 !important;\n        transform: translateX(25%) rotate(45deg) !important;\n        path { fill: #b99453 }\n      }\n    }\n  }\n\n  @at-root .UiBox:last-child {\n    #{$B} + & {\n      opacity: 1;\n      transition: opacity .19s .19s ease-out;\n    }\n    #{$B}#{$OPEN} + & {\n      @include Break( max-width Breaks(3) ){\n        opacity: .5;\n        mix-blend-mode: multiply;\n        > * { pointer-events: none !important }\n      }\n    }\n  }\n\n  &:after {\n    $matte: Color(light);\n    content: \" \";\n    z-index: 2;\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: -1px;\n    left: 0;\n    pointer-events: none;\n    background: linear-gradient(\n      to top,\n      $matte 3px,\n      rgba($matte,.3) 2em,\n      rgba($matte,0) 3em\n      );\n    opacity: .9;\n  }\n  &--ribbon {\n    // content: \"Filters\";\n    cursor: pointer;\n    appearance: none;\n    user-select: none;\n\n    z-index: 9;\n    position: absolute;\n    left: 100%;\n    top: 0;\n    \n    @extend %rack;\n\n    height: $ribbon-height;\n    min-width: 5.5em;\n    margin-left: $sidebar-width;\n    padding: .1em nth($sidebar-pad,2);\n\n    font: inherit;\n    font-size: .9em;\n    font-weight: 300;\n    text-transform: uppercase;\n    letter-spacing: .1em;\n    color: Color(light);\n    border-width: 0 !important;\n    outline: none;\n    background: Color(theme);\n    box-shadow: .5em .25em 2em -.25em rgba(Color(dark),.3);\n\n    transition: inherit;\n\n    #{$B}#{$OPEN} & {\n      // transition: .38s 0s ease-out;\n      min-width: 100%;\n      padding-left: nth($sidebar-pad,2);\n      padding-right: nth($sidebar-pad,2);\n      margin-left: -$sidebar-width;\n      color: Color(light);\n      background: Color(slate);\n      box-shadow: 2px 0 0 0 Color(dark);\n      box-shadow: 0 0 0 0 Color(dark);\n    }\n\n    & .UiIcon {\n      position: absolute;\n      right: nth($sidebar-pad,2);\n      margin-right: -5px;\n      opacity: 0;\n      transition: inherit;\n    }\n    #{$B}#{$OPEN} & .UiIcon {\n      opacity: .5;\n      transition-delay: 0s;\n    }\n    #{$B}#{$OPEN} &:hover .UiIcon {\n      transition-delay: .38s;\n      &:hover { transition-delay: .0s !important }\n    }\n    // #{$B}#{$OPEN} .UiIcon { filter: invert(.75) }\n    \n  }\n  &--header {\n    @extend %rack;\n    .UiButton {\n      font-size: 0.7em;\n      margin-top: -0.25 * nth($sidebar-pad,2);\n      margin-right: -0.25 * nth($sidebar-pad,2);\n    }\n  }\n  &--inner {\n    z-index: 1;\n    position: relative;\n    width: 100%;\n    max-height: 100%;\n    max-height: calc( 100% - #{$ribbon-height});\n    overflow: scroll;\n    -webkit-overflow-scrolling: touch;\n    margin-top: $ribbon-height;\n    padding: 0 nth($sidebar-pad,2);\n    background: transparent;\n    background: Color(light);\n    border-width: 0;\n\n    box-shadow: 1.5em -2.5em 3em -1.25em rgba(Color(dark),.08);\n    // box-shadow: 0 0 3em 0 rgba(black,.1);\n\n    >:first-child { margin-top:    2rem }\n    >:last-child  { margin-bottom: 2rem }\n  }\n  @include Break( max-width Breaks(3) ){\n    & {\n      top: 4rem;\n      // margin-top: 6.5rem;\n      margin-top: 2rem;\n      max-height: calc(100vh - $footer-height); // TODO: set global $footerHeight to 75px\n      box-shadow: 1em 0 3em -1em rgba(black,.15);\n    }\n    &#{$OPEN} {\n      overflow: hidden;\n      margin-right: -$sidebar-width;\n    }\n    &--inner {\n      min-height: 100%;\n      >:last-child { margin-bottom: 3rem }\n    }\n    @at-root #WineFiltersWrap > .UiPanel { flex-flow: nowrap row }\n    .UiHeading { text-align: left !important }\n  }\n}\n\n.WineSearch {\n  $B: &;\n  $search-input-height: 2.4rem;\n  &.hidden {\n    opacity: 0;\n  }\n  &_grid {\n    min-height: $search-input-height;\n    margin: 0 0 2.6rem !important;\n    transition: .38s .1s ease-out;\n\n    @include Break( min-width Breaks(3) ){\n      justify-content: flex-end;\n      align-items: center;\n      padding-left: 7.5rem;\n      transition: .33s 0s ease;\n      .WineFilters.open ~ & {\n        padding-left: 0;\n      }\n    }\n    @include Break( max-width Breaks(3) ){\n      flex-flow: nowrap column-reverse;\n      justify-content: flex-end;\n      align-items: center;\n      transform: translateY(-(3rem + $search-input-height/4));\n      margin: 0 0 ($search-input-height/4) !important;\n      /deep/ #{$B}--inputWrap {\n        margin-left: auto;\n        margin-bottom: 3rem + $search-input-height/4 !important;\n      }\n    }\n  }\n  .WineFilters & {\n    margin: 1.3rem 0 .7rem;\n    /deep/ &--inputWrap {\n      width: 100%;\n      input {\n        flex: 1;\n      }\n    }\n    .UiHeading {\n      display: none !important;\n    }\n  }\n}\n\n.FilterChits {\n  margin: 0 1em;\n  font-size: 0.8rem;\n  white-space: nowrap;\n  max-width: 80%;\n  overflow: hidden;\n  > * {\n    display: inline-flex;\n    color: Color(silver);\n    & + * {\n      margin-left: 0.3rem;\n    }\n    &:not(:last-child):after {\n      content: '/';\n      margin-left: 0.3rem;\n    }\n  }\n  + .WineFiltersWrap { margin-left: auto; }\n  @include Break( max-width Breaks(3) ){\n    margin: 0 auto;\n    order: -1;\n  }\n}\n","//////////////////////////////\n// Default Variables\n//////////////////////////////\n$Breakpoint-Settings: (\n  'default media': all,\n  'default feature': min-width,\n  'default pair': width,\n\n  'force all media type': false,\n  'to ems': false,\n  'transform resolutions': true,\n\n  'no queries': false,\n  'no query fallbacks': false,\n\n  'base font size': 16px,\n\n  'legacy syntax': false\n);\n\n$breakpoint: () !default;\n\n//////////////////////////////\n// Imports\n//////////////////////////////\n@import \"breakpoint/settings\";\n@import 'breakpoint/context';\n@import 'breakpoint/helpers';\n@import 'breakpoint/parsers';\n@import 'breakpoint/no-query';\n\n@import 'breakpoint/respond-to';\n\n@import \"breakpoint/legacy-settings\";\n\n//////////////////////////////\n// Breakpoint Mixin\n//////////////////////////////\n\n@mixin breakpoint($query, $no-query: false) {\n  @include legacy-settings-warning;\n\n  // Reset contexts\n  @include private-breakpoint-reset-contexts();\n\n  $breakpoint: breakpoint($query, false);\n\n  $query-string: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-brand: #D21034;\n$c-alt:   #7D1214;\n\n$colors:(\n  dark:      $c-black, // #0E0E0E\n  slate:     lighten($c-black,15%), // #323232,\n  silver:    lighten($c-black,67%), // #B8B8B8\n  cream:     lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  creamDark: mix(#BFB4A0,$c-black,75%),\n  light:     mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:     $c-matte, // #F0F0F0\n  theme:     $c-theme, // #BA9454\n  alt:       $c-alt,\n  brand:     $c-brand,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}"],"sourceRoot":""}]);

